import './sobre.css'

export default function Sobre(){
    return(
        <section className='sobre' id='sobre'>
            <h1 className='h1sobre'>SOBRE</h1>

            <hr style={{
                color: '#000000',
                 backgroundColor: '#000000',
                 height: 3,
                 width: "70%",
                borderColor : '#000000'}}/>

            <div className='containersobre'>
                <p className='psobre'>
                    Você quer se aprofundar na Palavra de Deus e descobrir o que ela tem a dizer sobre a sua vida? 
                    Então venha partifipar do nosso estudo bíblico!
                    Junte-se a outros jovens enquanto mergulhamos na Bíblia e aprendemos juntos sobre a verdadeira natureza de Deus e o plano 
                    que Ele tem para cada um de nós.
                    <br></br>
                    <br></br>
                    Neste estudo bíblico, nosso objetivo é explorar as Escrituras de forma aprofundada e aplicálas às nossas vidas diárias. 
                    Cada semana abordaremos um tema específico e usaremos a Sagrada regras de fé para aprofundar nossas 
                    compreensões e nos ajudar a crescer espiritualmente.
                    <br></br>
                    <br></br>
                    Não importa se você é um novo crente ou se já estudou a Bíblia por anos, 
                    este estudo é para todos os jovens que desejam crescer em seu relacionamento com Deus. Então, venha como você está e se junte a nós!
                </p>
                <p className='psobre'>
                    Você está procurando por um momento descontraído e agradável para se conectar com outros jovens e louvar a Deus? 
                    Acompanhe as nossas programações aqui pelo site aonde na aba "Programação" estão descritos os nossos próximos encontros.
                    <br></br>
                    <br></br>
                    Este é um espaço dedicado a comunhão e á adoração a Deus. Mós teremos muita música animada, jogos, lanches e tempo para conversar 
                    e rir juntos. Mas acima de tudo, nosso foco principal é glorificar a Deus e fortalecer nossos relacionamentos com Ele. 
                </p>          
            </div>
        </section>
    )
}