import './home.css'

function TextHome(){
    return(
        <div className='TextHome'>
            <h1>
                UPA - UNIÃO PRESBITERIANA DE ADOLESCENTES
            </h1>
            <p>
            A União Presbiteriana de Adolexcentes (UPA) é uma sociedade interna da Igreja Persbiteriana dedicada ao trabalho com 
            adolescentes de 12 a 18 anos. Tem como objetivo promover a integração e o desenvolvimento pessoal e espiritual de adolescentes 
            dentro do contexto da IPB (Igreja Presbiteriana do Brasil).
            <br></br>
            <br></br>
            Através de encontros e atividades educacionais, sociais e religiosas, a UPA busca proporcionar aos jovens um ambiente seguro e 
            acolhedor para que possam crescer em sua fé e em sua identidade como cidadãos comprometidos em seguir a palavra de Deus. 
            Principalmente como cristãos.
            </p>
        </div>
    )
}

export default function Home(){
    return(
        <section className="home">
            <TextHome/>
        </section>
    )
}